import React, {Component, PropTypes}  from "react";
import {render} from "react-dom";
import {Link} from 'react-router'
import {selectTab,fetchTopics,recordScrollT} from '../../actions/actions'
import {connect} from 'react-redux'
import Header from "../Common/Header/Header"
import Lists from "../Common/Lists/Lists"
class HomePage extends React.Component {
    constructor(props) {
        super(props);
        this.displayName = "HomePage";
        this.state = {};
        this.tabs = [
            {
                title: '全部',
                filter: 'all',
            },
            {
                title: '精华',
                filter: 'good',
            },
            {
                title: '分享',
                filter: 'share',
            },
            {
                title: '问答',
                filter: 'ask',
            },
            {
                title: '招聘',
                filter: 'job',
            }
        ];
        this.handleClick = (tab) => {
            const {selectedTab,dispatch} = this.props;
            dispatch(selectTab(tab));
            dispatch(fetchTopics(selectedTab));
        }
    }
    componentWillMount(){

        const {selectedTab,dispatch} = this.props;
        dispatch(fetchTopics(selectedTab));

    }

    render() {
        const {selectedTab,dispatch,topics}=this.props;
        return (
            <div>
                <Header tabs={this.tabs} filter={selectedTab} onClick={this.handleClick} >
                    {this.tabs.map((tab, index) =>
                        <Lists topics={topics} key={index}/>
                    )}
                </Header>
            </div>
        )
    }
}

function mapStateToProps(state) {
    const {homePage} = state;
    const {selectedTab,tabData} = homePage;
    // 当组件第一次render时,还未进行任何action,初始化的state里没有tabData[selectedTab]，所以这里需要初始化
    return {selectedTab,tabData}
}
export default connect(mapStateToProps) (HomePage)



